@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  font-family: 'Ubuntu', sans-serif;
  background-color: #141414;
  color: #ffffff;
  margin: 0;
  overflow: auto;
  height: 100%; }

mark {
  background-color: #c8ffc0;
  color: #212121; }

.layout {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start; }
  .layout.row {
    -webkit-flex-direction: row;
            flex-direction: row; }
  .layout.column {
    -webkit-flex-direction: column;
            flex-direction: column; }

.flex {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto; }

.full-page-loader.hidden {
  opacity: 0;
  pointer-events: none; }

.full-page-loader {
  -webkit-transition: opacity 1s ease;
  transition: opacity 1s ease;
  opacity: 1;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 2; }

@media all and (max-width: 736px) {
  .layout.column-lt-xs {
    -webkit-flex-direction: column;
            flex-direction: column; } }

.mail-icon {
  color: #ffff81; }

.location-icon {
  color: #ff6c6c; }

.phone-icon {
  color: #9dff9d; }

.mobile-icon {
  color: #33b2ff; }

.question-icon {
  color: #f6a8fb; }

.contact-details {
  margin: 10px; }
  .contact-details .information-header {
    font-weight: bold;
    display: block; }
  .contact-details .back-link {
    padding: 12px 40px;
    font-size: 20px;
    width: 100%;
    background-color: #212121;
    border-radius: 24px;
    display: block;
    margin-bottom: 10px; }
    .contact-details .back-link:hover {
      background-color: #2d2d2d; }
  .contact-details .contact {
    background-color: #212121;
    border-radius: 94px;
    padding: 20px;
    line-height: 1.4em; }
    .contact-details .contact img {
      border-radius: 50%;
      border: 10px solid;
      margin-right: 20px;
      width: 148px;
      height: 148px; }
    .contact-details .contact h2 {
      margin: 0; }
    .contact-details .contact .information {
      padding-right: 20px; }
    .contact-details .contact p {
      margin-bottom: 0;
      word-break: break-word; }
  @media all and (max-width: 960px) {
    .contact-details {
      font-size: 14px; }
      .contact-details .contact {
        border-radius: 74px; }
        .contact-details .contact h2 {
          margin-top: 1em; }
        .contact-details .contact img {
          border-width: 5px;
          width: 108px;
          height: 108px; } }

.contact-list ul {
  list-style: none;
  margin: 0;
  padding: 0; }

.contact-list ul.contacts {
  padding-top: 96px; }
  .contact-list ul.contacts li {
    margin: 10px;
    cursor: pointer; }

.contact-list ul.order-by {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; }
  .contact-list ul.order-by li {
    margin: 0px 5px;
    padding: 5px 10px;
    border-radius: 10px;
    text-transform: capitalize;
    background-color: #212121;
    cursor: pointer; }
    .contact-list ul.order-by li:hover {
      background-color: #2d2d2d; }
    .contact-list ul.order-by li.active {
      background-color: #c8ffc0;
      color: #212121; }
      .contact-list ul.order-by li.active:hover {
        background-color: #defdda; }

.contact-list .search-field {
  background-color: #212121;
  margin: 10px;
  border-radius: 24px; }
  .contact-list .search-field:hover {
    background-color: #2d2d2d; }
  .contact-list .search-field input {
    background-color: transparent;
    border: none;
    color: inherit;
    width: 100%;
    font-family: inherit;
    padding: 12px 40px;
    font-size: 20px; }

.contact-list .top-section {
  position: fixed;
  z-index: 1;
  top: 0;
  max-width: 100%;
  width: 960px;
  background-color: #141414;
  padding-bottom: 10px; }

.contact-list-item {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  background-color: #212121;
  border-radius: 100px; }
  .contact-list-item:hover {
    background-color: #2d2d2d;
    text-decoration: none; }
  .contact-list-item .svg-inline--fa {
    width: 26px;
    padding-right: 10px;
    display: inline-block; }
  .contact-list-item h2, .contact-list-item p {
    margin-top: 0px;
    margin-bottom: 5px; }
  .contact-list-item p {
    word-break: break-word; }
  .contact-list-item img {
    border-radius: 50%;
    border: 5px solid;
    display: -webkit-flex;
    display: flex;
    width: 110px;
    height: 110px; }
  .contact-list-item .text {
    max-height: 80px;
    max-width: calc(100% - 130px);
    -webkit-flex: 1 1;
            flex: 1 1; }
  .contact-list-item .email {
    word-break: break-all; }
  @media all and (max-width: 960px) {
    .contact-list-item {
      -webkit-align-items: flex-start;
              align-items: flex-start;
      border-radius: 50px; }
      .contact-list-item h2 {
        font-size: 16px; }
      .contact-list-item p {
        font-size: 14px; }
      .contact-list-item img {
        width: 50px;
        height: 50px;
        border-width: 3px; }
      .contact-list-item .text {
        max-height: 123px;
        max-width: calc(100% - 60px);
        margin-left: 10px; } }

